
import React from 'react';
import { Link } from 'react-scroll';
import "./Menu.css";
import image from '../../assests/image.jpg';
import Zoom from 'react-reveal/Zoom';
import Fade from 'react-reveal/Fade';

const Menu = ({ toggle }) => {
    return (
        <>
            {toggle ? (
                <>
                    <Zoom>
                        <div className="navbar-profile-pic image">
                            <img style={{ maxWidth: '100%', maxHeight: '100%', objectFit: 'contain', marginLeft: '35px' }}  src={image} alt="Profile pic" />
                        </div>
                    </Zoom>
                    <Fade left>
                        <div className="nav-items">
                            <div className="nav-item">
                                <div className="nav-links">
                                    <Link
                                        to='Home'
                                        duration={100}
                                        spy={true}
                                        smooth={true}
                                        offset={-100}
                                        className="nav-link"
                                        activeClass="active">
                                        <i className="fa-solid fa-house" title='Home'></i>Home
                                    </Link>
                                </div>

                                <div className="nav-links">
                                    <Link
                                        to='About'
                                        duration={100}
                                        spy={true}
                                        smooth={true}
                                        offset={-100}
                                        className="nav-link"
                                        activeClass="active">
                                        <i className="fa-solid fa-address-card" title='About'></i>About
                                    </Link>
                                </div>

                                <div className="nav-links">
                                    <Link
                                        to='Education-Details'
                                        duration={100}
                                        spy={true}
                                        smooth={true}
                                        offset={-100}
                                        className="nav-link"
                                        activeClass="active">
                                        <i className="fa-solid fa-school" title='Education Details'></i>Education Details
                                    </Link>
                                </div>

                                <div className="nav-links">
                                    <Link
                                        to='Text-stack'
                                        duration={100}
                                        spy={true}
                                        smooth={true}
                                        offset={-100}
                                        className="nav-link"
                                        activeClass="active">
                                        <i className="fa-solid fa-microchip" title='Text stack'></i>Text stack
                                    </Link>
                                </div>

                                <div className="nav-links">
                                    <Link
                                        to='Projects'
                                        duration={100}
                                        spy={true}
                                        smooth={true}
                                        offset={-100}
                                        className="nav-link"
                                        activeClass="active">
                                        <i className="fa-solid fa-diagram-project" title='Projects'></i>Projects
                                    </Link>
                                </div>

                                <div className="nav-links">
                                    <Link
                                        to='work-Experince'
                                        duration={100}
                                        spy={true}
                                        smooth={true}
                                        offset={-100}
                                        className="nav-link"
                                        activeClass="active">
                                        <i className="fa-brands fa-creative-commons-nd" title='work Experince'></i>work Experince
                                    </Link>
                                </div>

                                <div className="nav-links">
                                    <Link
                                        to='Contact'
                                        duration={100}
                                        spy={true}
                                        smooth={true}
                                        offset={-100}
                                        className="nav-link"
                                        activeClass="active">
                                        <i className="fa-solid fa-phone" title='Contact'></i>Contact
                                    </Link>
                                </div>
                            </div>
                        </div>
                    </Fade>
                </>
            ) : (
                <div className="nav-items">
                            <div className="nav-item">
                                <div className="nav-links">
                                    <Link
                                        to='Home'
                                        duration={100}
                                        spy={true}
                                        smooth={true}
                                        offset={-100}
                                        className="nav-link"
                                        activeClass="active">
                                        <i className="fa-solid fa-house" title='Home'></i>
                                    </Link>
                                </div>

                                <div className="nav-links">
                                    <Link
                                        to='About'
                                        duration={100}
                                        spy={true}
                                        smooth={true}
                                        offset={-100}
                                        className="nav-link"
                                        activeClass="active">
                                        <i className="fa-solid fa-address-card" title='About'></i>
                                    </Link>
                                </div>

                                <div className="nav-links">
                                    <Link
                                        to='Education-Details'
                                        duration={100}
                                        spy={true}
                                        smooth={true}
                                        offset={-100}
                                        className="nav-link"
                                        activeClass="active">
                                        <i className="fa-solid fa-school" title='Education Details'></i>
                                    </Link>
                                </div>

                                <div className="nav-links">
                                    <Link
                                        to='Text-stack'
                                        duration={100}
                                        spy={true}
                                        smooth={true}
                                        offset={-100}
                                        className="nav-link"
                                        activeClass="active">
                                        <i className="fa-solid fa-microchip" title='Text stack'></i>
                                    </Link>
                                </div>

                                <div className="nav-links">
                                    <Link
                                        to='Projects'
                                        duration={100}
                                        spy={true}
                                        smooth={true}
                                        offset={-100}
                                        className="nav-link"
                                        activeClass="active">
                                        <i className="fa-solid fa-diagram-project" title='Projects'></i>
                                    </Link>
                                </div>

                                <div className="nav-links">
                                    <Link
                                        to='work-Experince'
                                        duration={100}
                                        spy={true}
                                        smooth={true}
                                        offset={-100}
                                        className="nav-link"
                                        activeClass="active">
                                        <i className="fa-brands fa-creative-commons-nd" title='work Experince'></i>
                                    </Link>
                                </div>

                                <div className="nav-links">
                                    <Link
                                        to='Contact'
                                        duration={100}
                                        spy={true}
                                        smooth={true}
                                        offset={-100}
                                        className="nav-link"
                                        activeClass="active">
                                        <i className="fa-solid fa-phone" title='Contact'></i>
                                    </Link>
                                </div>
                            </div>
                        </div>



                // <div className="nav-items">
                //     <div className="nav-item">
                //         <div className="nav-links">
                //             <i className="fa-solid fa-house" title='Home'></i>
                //         </div>

                //         <div className="nav-links">
                //             <i className="fa-solid fa-address-card" title='About'></i>
                //         </div>

                //         <div className="nav-links">
                //             <i className="fa-solid fa-school" title='Education Details'></i>
                //         </div>

                //         <div className="nav-links">
                //             <i className="fa-solid fa-microchip" title='Text stack'></i>
                //         </div>

                //         <div className="nav-links">
                //             <i className="fa-solid fa-diagram-project" title='Projects'></i>
                //         </div>

                //         <div className="nav-links">
                //             <i className="fa-brands fa-creative-commons-nd" title='work Experince'></i>
                //         </div>

                //         <div className="nav-links">
                //             <i className="fa-solid fa-phone" title='Contact'></i>
                //         </div>
                //     </div>
                // </div>
            )}
        </>
    );
}

export default Menu;




