import React from 'react';
import { useTheme } from '../../context/ThemeContext';
import Resume2 from "../../assests/Docs/Rahul_Nagaura.pdf"
import Typewriter from 'typewriter-effect';
import Fade from 'react-reveal/Fade';
import { Link } from 'react-scroll'
import "./home.css";


const Home = ({ toggle }) => {
    const [theme, setTheme] = useTheme();

    const handleTheme = () => {
        setTheme((prevState) => (prevState === "light" ? "dark" : "light"))
    }
    // 
    return (
        <>
            {toggle ? (<>

                <div id='Home' className="container-fluid home-container home1 home23">
                    <div className="theme-btn" onClick={handleTheme} >
                        {theme === "light" ? (<i className="fa-solid theme-icon fa-moon"></i>) : (<i className="fa-solid theme-icon fa-sun"></i>)}
                    </div>
                    <div className="container  home-content">
                        <Fade right>
                            <h1>Hi I'm a</h1>
                            <h2>
                                <Typewriter
                                    options={{
                                        strings: ['Full stack developer !', 'MERN stack developer !', ' React developer !'],
                                        autoStart: true,
                                        loop: true,
                                    }}
                                />
                            </h2>
                        </Fade>
                        <Fade bottom>
                            <div className="home-buttons">
                                <a className="btn btn-hire"
                                    href='https://api.whatsapp.com/send?phone=918890903110'
                                    rel='noreferrer'
                                    target='_blank'>Hire Me</a>

                                <a className="btn btn-cv" href={Resume2} download="Rahul Resume.pdf">My Resume</a>
                            </div>
                        </Fade>
                    </div>
                </div> </>) : (<>

                    <div id='Home' className="container-fluid home-container home23">
                        <div className="theme-btn" onClick={handleTheme} >
                            {theme === "light" ? (<i className="fa-solid theme-icon fa-moon"></i>) : (<i className="fa-solid theme-icon fa-sun"></i>)}
                        </div>
                        <div className="container home-content ">
                            <Fade right>
                                <h1>Hi I'm a</h1>
                                <h2>
                                    <Typewriter
                                        options={{
                                            strings: ['Full stack developer !', 'MERN stack developer !', ' React developer !'],
                                            autoStart: true,
                                            loop: true,
                                        }}
                                    />
                                </h2>
                            </Fade>
                            <Fade bottom>
                                <div className="home-buttons">
                                    <a className="btn btn-hire"
                                        href='https://api.whatsapp.com/send?phone=918890903110'
                                        rel='noreferrer'
                                        target='_blank'>Hire Me</a>
                                    <Link className="btn btn-cv" to={Resume2} download="Rahul Resume.pdf">My Resume</Link>
                                </div>
                            </Fade>
                        </div>
                    </div></>)
            }
        </>
    )
}

export default Home
