import React from 'react';
import { VerticalTimeline, VerticalTimelineElement } from 'react-vertical-timeline-component';
import 'react-vertical-timeline-component/style.min.css';
import "./Education.css";
const Education = () => {
    return (
        <>
            <div className="education" id='Education-Details' >
                <h2 className='text-center mt-3 mb-1 col-12 text-uppercase'>Education Details</h2>
                <hr />
                <VerticalTimeline>
                    <VerticalTimelineElement
                        className="vertical-timeline-element--work custom-timeline-element"
                        contentStyle={{ background: 'white', color: 'black' }}
                        contentArrowStyle={{ borderRight: '7px solid  white' }}
                        date="2022-2026"
                        iconStyle={{ background: '#138781', color: '#fff' }}
                        icon={<i className="fa-solid fa-graduation-cap"></i>}
                    >
                        {/* <h3 className="vertical-timeline-element-title">
                            Bachelor of Technology (B.Tech) in Chemical Engineering
                        </h3>
                        <h4 className="vertical-timeline-element-subtitle">
                            Indian Institute of Technology (Indian School of Mines), Dhanbad
                        </h4> */}
                        <h3 className="vertical-timeline-element-title">Bachelor of Technology in Chemical Engineering</h3>
                        <h4 className="vertical-timeline-element-subtitle">IIT (ISM) Dhanbad</h4>

                    </VerticalTimelineElement>
                    {/* <VerticalTimelineElement
                        className="vertical-timeline-element--work"
                        contentStyle={{ background: 'white', color: 'black' }}
                        contentArrowStyle={{ borderRight: '7px solid  white' }}
                        date="2022-2026"
                        iconStyle={{ background: '#138781', color: '#fff' }}
                        icon={<i className="fa-solid fa-graduation-cap"></i>}
                    >
                        <h3 className="vertical-timeline-element-title">BTech</h3>
                        <h4 className="vertical-timeline-element-subtitle">IIT (ISM) Dhanbad</h4>

                    </VerticalTimelineElement> */}
                </VerticalTimeline>
            </div>
        </>
    )
}

export default Education