import React, { useState } from 'react'
import "./MobileNav.css";
import { Link } from 'react-scroll';
const MobileNav = () => {
    const [open, setOpen] = useState(false);

    const handelOpen = () => {
        setOpen(!open);
    }

    const handleMenuClick = () => {
        setOpen(false);
    }
    return (
        <>
            <div className="mobile-nav">
                <div className="mobile-nav-header">
                    <i onClick={handelOpen} class="fa-solid fa-bars mobile-icon"></i>
                    <span className='nav-title'>My Portfolio App</span>
                </div>
                {open && (
                    <div className="mobile-nav-menu">
                        <div className="nav-items">
                            <div className="nav-item">
                                <div className="nav-links">
                                    <Link
                                        to='Home'
                                        duration={100}
                                        spy={true}
                                        smooth={true}
                                        offset={-100}
                                        className="nav-link"
                                        onClick={handleMenuClick}
                                        activeClass="active">

                                        <i className="fa-solid fa-house" title='Home'></i>Home
                                    </Link>
                                </div>

                                <div className="nav-links">
                                    <Link
                                        to='About'
                                        duration={100}
                                        spy={true}
                                        smooth={true}
                                        offset={-100}
                                        className="nav-link"
                                        onClick={handleMenuClick}
                                        activeClass="active">
                                        <i className="fa-solid fa-address-card" title='About'></i>About
                                    </Link>
                                </div>

                                <div className="nav-links">
                                    <Link
                                        to='Education-Details'
                                        duration={100}
                                        spy={true}
                                        smooth={true}
                                        offset={-100}
                                        className="nav-link"
                                        onClick={handleMenuClick}
                                        activeClass="active">
                                        <i className="fa-solid fa-school" title='Education Details'></i>Education Details
                                    </Link>
                                </div>

                                <div className="nav-links">
                                    <Link
                                        to='Text-stack'
                                        duration={100}
                                        spy={true}
                                        smooth={true}
                                        offset={-100}
                                        className="nav-link"
                                        onClick={handleMenuClick}
                                        activeClass="active">
                                        <i className="fa-solid fa-microchip" title='Text stack'></i>Text stack
                                    </Link>
                                </div>

                                <div className="nav-links">
                                    <Link
                                        to='Projects'
                                        duration={100}
                                        spy={true}
                                        smooth={true}
                                        offset={-100}
                                        className="nav-link"
                                        onClick={handleMenuClick}
                                        activeClass="active">
                                        <i className="fa-solid fa-diagram-project" title='Projects'></i>Projects
                                    </Link>
                                </div>

                                <div className="nav-links">
                                    <Link
                                        to='work-Experince'
                                        duration={100}
                                        spy={true}
                                        smooth={true}
                                        offset={-100}
                                        className="nav-link"
                                        onClick={handleMenuClick}
                                        activeClass="active">
                                        <i className="fa-brands fa-creative-commons-nd" title='work Experince'></i>work Experince
                                    </Link>
                                </div>

                                <div className="nav-links">
                                    <Link
                                        to='Contact'
                                        duration={100}
                                        spy={true}
                                        smooth={true}
                                        offset={-100}
                                        className="nav-link"
                                        onClick={handleMenuClick}
                                        activeClass="active">
                                        <i className="fa-solid fa-phone" title='Contact'></i>Contact
                                    </Link>
                                </div>
                            </div>
                        </div>
                    </div>
                )}
            </div>
        </>
    )
}

export default MobileNav
