import React from 'react'
import TechstackList from '../../Utills/TechstackList'
import RubberBand from 'react-reveal/RubberBand';
import Fade from 'react-reveal/Fade';
import "./Techstack.css"
const Techstack = () => {
    return (
        <>
            <div id='Text-stack' className="container techstack">
                <RubberBand>
                    <h2 className='text-center mt-3 mb-1 col-12 text-uppercase'>Technology Stacks</h2>
                    <hr />
                    <p>
                        I am learning to build responsive and dynamic web applications using HTML, CSS, JavaScript, and React for the frontend. For backend development, I am working with Node.js and Express.js, and using MongoDB as my database solution to create robust and scalable web applications.
                    </p>
                </RubberBand>
                <div className='row'>
                    {TechstackList.map((tech) => (
                        <Fade right>
                            <div key={tech._id} className="col-md-3">
                                <div className="card m-2">
                                    <div className="card-content">
                                        <div className="card-body">
                                            <div className="media d-flex justify-content-center">
                                                <div className="align-self-center">
                                                    {tech.icon}
                                                </div>
                                                <div className="media-body">
                                                    <h5>{tech.name}</h5>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </Fade>
                    ))
                    }
                </div>
            </div>
        </>
    )
}

export default Techstack
