import React, { useState } from 'react';
import "../../compontent/layout/layout.css";
import Home from "../../pages/Home/home";
import Menu from "../Menu/Menu.js"

const Layout = () => {
    const [toggle, setToggle] = useState(true);

    const handleToggle = () => {
        setToggle(!toggle);
    }

    return (
        <div className="sidebar-section">
            <div className={toggle ? ("sidebar-toggle") : ("sidebar")}>
                <div className="sidebar-toggle-icons">
                    <p className='icon' onClick={handleToggle}>
                        {toggle ? (
                            <i className="fa-solid icon-angle fa-angles-left"></i>
                        ) : (
                            <i className="fa-solid icon-angle fa-angles-right"></i>
                        )}
                    </p>
                </div>
                <Menu toggle={toggle} />
            </div>
            <div className="container">
                <Home toggle={toggle} />
            </div>
        </div>
    );
}

export default Layout;
