import React from 'react';
import { VerticalTimeline, VerticalTimelineElement } from 'react-vertical-timeline-component';
import 'react-vertical-timeline-component/style.min.css';
import "./WorkExp.css"
const WorkExp = () => {
    return (
        <>
            <div className="work" id='work-Experince'>
                <div className="container work-exp">
                    <h2 className='text-center mt-3 mb-1 col-12 text-uppercase'>Work Experience</h2>
                    <hr />

                    {/* <VerticalTimeline lineColor='#1e2e2c' > */}
                    <VerticalTimeline  >
                        <VerticalTimelineElement 
                            className="vertical-timeline-element--work"
                            contentStyle={{ background: 'white', color: '#1e2e2c' }}
                            contentArrowStyle={{ borderRight: '7px solid #1387812a' }}
                            date="2022-2026"
                            iconStyle={{ background: '#1e2e2c', color: '#fff' }}
                            icon={<i className="fa-solid fa-graduation-cap"></i>}
                        >
                            <h3 className="vertical-timeline-element-title">Not Yet</h3>
                            <h4 className="vertical-timeline-element-subtitle">Fresher</h4>

                        </VerticalTimelineElement>
                    </VerticalTimeline>
                </div>
            </div>
        </>

    )
}

export default WorkExp
