import React, { useState } from 'react';
import "./Contact.css";
import { toast } from 'react-toastify';
import axios from "axios";
// import { useTheme } from '../../context/ThemeContext';

const Contact = () => {
    const [name, setName] = useState("");
    const [email, setEmail] = useState("");
    const [message, setMessage] = useState("");
    // const [theme, setTheme] = useTheme();

    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            if (!name || !email || !message) {
                toast.error("Please Provide all fields");
            }
            const res = await axios.post("/api/v1/portfolio/sendEmail", { name, email, message });
            if (res.data.success) {
                toast.success(res.data.message);
                setName("");
                setEmail("");
                setMessage("");
            } else {
                toast.error(res.data.message);
            }
        } catch (err) {
            console.log(err);
        }
    }
    return (
        <>
            <div className="contact" id='Contact'>
                <div className="card contact-2 card0 border-0">
                    <div className="row">
                        <div className="col-md-6 col-lg-6 col-xl-6 col-sm-12">
                            <div className="card1">
                                <div className="row border-line box-contact">
                                    <h2 className="heading-contact center">CONTACT</h2>
                                    <p className="center">| I AM OPEN TO WORK AND NETWORK |</p>
                                    <p className="center">
                                        If you have an exciting project in mind, want to collaborate, or simply wish to connect, let's initiate a conversation.
                                    </p>
                                    {/* <img
                                        src="https://encrypted-tbn2.gstatic.com/images?q=tbn:ANd9GcR7cZ7EVonFVRdgG4jjn8PJdjA7KEeKZl2d9FNtVoXhimMiIFgF"
                                        alt="" className='image' /> */}
                                </div>
                            </div>
                        </div>
                        <div className="col-lg-6 col-md-6 mt-3 ">
                            <div className="card2 d-flex card border-0 px-4 py-5 contact-3">
                                <div className="row ">
                                    <div className="row">
                                        <h6 className='heading'>Contact With Me
                                            <a rel="noreferrer" href="https://www.linkedin.com/in/rahul-nagaura/" target="_blank">
                                                <i color='blue' className="contact-icon linkeden fa-brands fa-linkedin"></i>
                                            </a>
                                            <a rel="noreferrer" href="https://github.com/rahul-nagaura" target="_blank" >
                                                <i className="contact-icon github fa-brands fa-github"></i>
                                            </a>
                                            <a rel="noreferrer" href="https://api.whatsapp.com/send?phone=918890903110"
                                                target="_blank">
                                                <i className="contact-icon whatsapp fa-brands fa-whatsapp"></i>
                                            </a>
                                            <a rel="noreferrer" target="_blank" href="https://www.instagram.com/rahul_nagaura/">
                                                <i className="contact-icon facebook fa-brands fa-instagram"></i>
                                            </a>

                                        </h6>
                                    </div>

                                    <div className="row px-3 mb-4">
                                        <div className="line" />
                                        <small className="or text-center">OR</small>
                                        <div className="line" />
                                    </div>
                                    <div className="row px-3">
                                        <input
                                            type='text'
                                            name='name'
                                            placeholder='Enter Your Name'
                                            value={name}
                                            onChange={(e) => setName(e.target.value)}
                                            className='mt-3' />
                                    </div>
                                    <div className="row px-3">
                                        <input
                                            type='email'
                                            name='email'
                                            placeholder='Enter Your Eamil'
                                            value={email}
                                            onChange={(e) => setEmail(e.target.value)}
                                            className='mt-3' />
                                    </div>
                                    <div className="row px-3">
                                        <textarea
                                            type='text'
                                            name='name'
                                            placeholder='Enter Your Message'
                                            value={message}
                                            onChange={(e) => setMessage(e.target.value)}
                                            className='mt-3' />
                                    </div>
                                    <div className="row px-3">
                                        <button onClick={handleSubmit} type='submit' className='mt-3 button'>
                                            Send Message
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Contact
