import React from 'react'
import Jump from 'react-reveal/Jump';
import "./About.css";
import image from '../../assests/image.jpg';
const About = () => {
  return (
    <>
      <Jump>
        <div className="about" id='About'>
          <div className="row">
            <div className="col-md-6 col-xl-6 col-lg-6 col-xs-12 about-image">
              <img className='about-imamge' src={image} alt="progile-pic" />
            </div>
            <div className="col-md-6 col-xl-6 col-lg-6 col-xs-12 about-container ">
              <h1 >About Me</h1>
              <p>
                Hi, I'm Rahul Nagaura, and I'm currently pursuing a B.Tech in Chemical Engineering from IIT (ISM) Dhanbad. Originally from Sikar, Rajasthan, I have a passion for both chemical engineering and web development.

                Despite some challenges in my academic journey, I have developed strong analytical skills and a problem-solving mindset. Recently, I've expanded my interests to include web development, where I'm learning to build engaging and responsive websites.

                Through my web development studies, I've gained proficiency in HTML, CSS, and JavaScript, and have also explored frameworks and libraries such as React and Bootstrap. Additionally, I have experience with backend technologies like Node.js and Express, as well as database management with MongoDB. I'm dedicated to continuous learning and am excited to apply my skills in real-world projects.

                I'm enthusiastic about collaborating on new projects and connecting with like-minded individuals. Feel free to reach out to me at <a target='_blank' href='mailto:rahulnagora88901@gmail.com' rel="noreferrer"  >Eamil</a> or connect with me on <a rel="noreferrer"  target='_blank' href='https://www.linkedin.com/in/rahul-nagaura/'>LinkedIn</a> .


              </p>
            </div>
          </div>
        </div>
      </Jump>
    </>
  )
}

export default About


